.ContainerNavGoBack{
    height: 55px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background: #3f4035;
    width: 100vw;
    display: flex;
    align-items: center;
}
.ContainerNavGoBack button{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background: transparent;
    border: none;
    font-size: 30px;
    padding: 10px;
}